export default "@import '~xtend-library/src/extension/overlay/position-setup.less';\n\n/**\n * xtend extension: overlay-position\n */\n\n.overlay-position {\n  .overlay {\n    .overlay-position--left& {\n      width: 88% !important;\n      max-width: 500px !important;\n    }\n    .overlay-position--right& {\n      width: 88% !important;\n      max-width: 500px !important;\n    }\n  }\n  // option\n  .overlay--dark();\n  .overlay--squared();\n  .overlay--noborder();\n}\n\n// animation\n\n.overlay-position {\n  animation-duration: @time--medium + @time--small; // needed for .out animation timing, put max animation time\n  &.active {\n    &.overlay-position--left {\n      .overlay_design {\n        .anim-x.active();\n      }\n      .overlay_content, .btn--close {\n        .anim-x.active();\n        animation-delay: @time--small;\n      }\n    }\n    &.overlay-position--right {\n      .overlay_design {\n        .anim-x-inverse.active();\n      }\n      .overlay_content, .btn--close {\n        .anim-x-inverse.active();\n        animation-delay: @time--small;\n      }\n    }\n  }\n  &.out {\n    &.overlay-position--left {\n      .overlay_design {\n        .anim-x-inverse.out();\n        animation-delay: @time--small;\n      }\n      .overlay_content, .btn--close {\n        .anim-x-inverse.out();\n      }\n    }\n    &.overlay-position--right {\n      .overlay_design {\n        .anim-x.out();\n        animation-delay: @time--small;\n      }\n      .overlay_content, .btn--close {\n        .anim-x.out();\n      }\n    }\n  }\n}\n"