export default "@import '~xtend-library/src/core/animation/animation-setup.less';\n\n/**\n * vars\n */\n\n// time\n\n@time--tiny: 100ms;\n@time--small: 250ms;\n@time--medium: 500ms;\n@time--big: 1000ms;\n@time--giant: 2000ms;\n\n// beziers\n\n@bez: @bez-cheetah;\n@bez--in: @bez-cheetah;\n@bez--out: @bez-cheetah;\n\n@bez-ease: ease; // others at https://easings.net/\n@bez-cheetah: ~'cubic-bezier(.14,.63,0,1)';\n@bez-tiger: ~'cubic-bezier(.96,.1,.14,.88)';\n@bez-cat: ~'cubic-bezier(.77,.0,.17,1)';\n@bez-penguin--in: ~'cubic-bezier(.36,0,0,1)';\n@bez-penguin--out: ~'cubic-bezier(1,0,.64,1)';\n@bez-penguin--in-out: ~'cubic-bezier(.68,.13,.25,1)';\n\n/**\n * transition\n */\n\n/* USAGE:\nopacity: 0;\ntransform: translateX(-1.5rem);\ntransition-property: all;\ntransition-duration: @time--medium;\ntransition-timing-function: @bez--out;\n&:hover, &.hover, &:active, &.active {\n  opacity: 1;\n  transform: translateX(0);\n  transition-property: all;\n  transition-duration: @time--medium;\n  transition-timing-function: @bez--in;\n}\n*/\n\n/**\n * animation\n */\n\n/* USAGE:\n.anim-opacity();\n*/\n\n/* USAGE:\n.anim-opacity.active();\n&:hover, &.hover, &:active, &.active {\n  .anim-opacity.out();\n}\n*/\n\n/* USAGE:\n&.active {\n  .inner {\n    .anim-opacity.active();\n  }\n}\n&.out {\n  .inner {\n    .anim-opacity.out();\n  }\n}\n*/\n\n// anim-opacity\n\n.anim-opacity {\n  &.active {\n    opacity: 0;\n    animation-name: anim-opacity--in;\n    animation-duration: @time--medium;\n    animation-timing-function: @bez--in;\n    animation-fill-mode: forwards;\n  }\n  &.out {\n    opacity: 1;\n    animation-name: anim-opacity--out;\n    animation-duration: @time--medium;\n    animation-timing-function: @bez--out;\n    animation-fill-mode: forwards;\n  }\n}\n\n@keyframes anim-opacity--in {\n  0% {\n    opacity: 0;\n  }\n  100% {\n    opacity: 1;\n  }\n}\n\n@keyframes anim-opacity--out {\n  0% {\n    opacity: 1;\n  }\n  100% {\n    opacity: 0;\n  }\n}\n\n// anim-zoom\n\n.anim-zoom {\n  &.active {\n    opacity: 0;\n    animation-name: anim-zoom--in;\n    animation-duration: @time--medium;\n    animation-timing-function: @bez--in;\n    animation-fill-mode: forwards;\n  }\n  &.out {\n    opacity: 1;\n    animation-name: anim-zoom--out;\n    animation-duration: @time--medium;\n    animation-timing-function: @bez--out;\n    animation-fill-mode: forwards;\n  }\n}\n\n@keyframes anim-zoom--in {\n  0% {\n    opacity: 0;\n    transform: scale(.75, .75);\n  }\n  100% {\n    opacity: 1;\n    transform: scale(1);\n  }\n}\n\n@keyframes anim-zoom--out {\n  0% {\n    opacity: 1;\n    transform: scale(1);\n  }\n  100% {\n    opacity: 0;\n    transform: scale(.75, .75);\n  }\n}\n\n// anim-y\n\n.anim-y {\n  &.active {\n    opacity: 0;\n    animation-name: anim-y--in;\n    animation-duration: @time--medium;\n    animation-timing-function: @bez--in;\n    animation-fill-mode: forwards;\n  }\n  &.out {\n    opacity: 1;\n    animation-name: anim-y--out;\n    animation-duration: @time--medium;\n    animation-timing-function: @bez--out;\n    animation-fill-mode: forwards;\n  }\n}\n\n@keyframes anim-y--in {\n  0% {\n    opacity: 0;\n    transform: translateY(-1.5rem);\n  }\n  100% {\n    opacity: 1;\n    transform: translateY(0);\n  }\n}\n\n@keyframes anim-y--out {\n  0% {\n    opacity: 1;\n    transform: translateY(0);\n  }\n  100% {\n    opacity: 0;\n    transform: translateY(1.5rem);\n  }\n}\n\n// anim-y-inverse\n\n.anim-y-inverse {\n  &.active {\n    opacity: 0;\n    animation-name: anim-y-inverse--in;\n    animation-duration: @time--medium;\n    animation-timing-function: @bez--in;\n    animation-fill-mode: forwards;\n  }\n  &.out {\n    opacity: 1;\n    animation-name: anim-y-inverse--out;\n    animation-duration: @time--medium;\n    animation-timing-function: @bez--out;\n    animation-fill-mode: forwards;\n  }\n}\n\n@keyframes anim-y-inverse--in {\n  0% {\n    opacity: 0;\n    transform: translateY(1.5rem);\n  }\n  100% {\n    opacity: 1;\n    transform: translateY(0);\n  }\n}\n\n@keyframes anim-y-inverse--out {\n  0% {\n    opacity: 1;\n    transform: translateY(0);\n  }\n  100% {\n    opacity: 0;\n    transform: translateY(-1.5rem);\n  }\n}\n\n// anim-x\n\n.anim-x {\n  &.active {\n    opacity: 0;\n    animation-name: anim-x--in;\n    animation-duration: @time--medium;\n    animation-timing-function: @bez--in;\n    animation-fill-mode: forwards;\n  }\n  &.out {\n    opacity: 1;\n    animation-name: anim-x--out;\n    animation-duration: @time--medium;\n    animation-timing-function: @bez--out;\n    animation-fill-mode: forwards;\n  }\n}\n\n@keyframes anim-x--in {\n  0% {\n    opacity: 0;\n    transform: translateX(-1.5rem);\n  }\n  100% {\n    opacity: 1;\n    transform: translateX(0);\n  }\n}\n\n@keyframes anim-x--out {\n  0% {\n    opacity: 1;\n    transform: translateX(0);\n  }\n  100% {\n    opacity: 0;\n    transform: translateX(1.5rem);\n  }\n}\n\n// anim-x-inverse\n\n.anim-x-inverse {\n  &.active {\n    opacity: 0;\n    animation-name: anim-x-inverse--in;\n    animation-duration: @time--medium;\n    animation-timing-function: @bez--in;\n    animation-fill-mode: forwards;\n  }\n  &.out {\n    opacity: 1;\n    animation-name: anim-x-inverse--out;\n    animation-duration: @time--medium;\n    animation-timing-function: @bez--out;\n    animation-fill-mode: forwards;\n  }\n}\n\n@keyframes anim-x-inverse--in {\n  0% {\n    opacity: 0;\n    transform: translateX(1.5rem);\n  }\n  100% {\n    opacity: 1;\n    transform: translateX(0);\n  }\n}\n\n@keyframes anim-x-inverse--out {\n  0% {\n    opacity: 1;\n    transform: translateX(0);\n  }\n  100% {\n    opacity: 0;\n    transform: translateX(-1.5rem);\n  }\n}\n\n/**\n * custom\n */\n\n// collapse\n\n.collapse--height, .collapse--width {\n  &.active {\n    transition-property: height, width;\n    transition-duration: @time--medium;\n    transition-delay: initial;\n    transition-timing-function: @bez--in;\n  }\n  &.out {\n    transition-property: height, width;\n    transition-duration: @time--medium;\n    transition-delay: initial;\n    transition-timing-function: @bez--out;\n  }\n}\n"