export default "@import '~xtend-library/src/core/drop/drop-setup.less';\n\n/**\n * vars\n */\n\n@drop--space: 6px; // space between button and drop\n\n/**\n * drop\n */\n\n.drop {\n  // styles\n  > .drop_inner {\n    > .drop_content {\n      > .list, > .list-block {\n        .padding(y, 1.3rem); // vertical space\n        min-width: 18rem;\n      }\n    }\n    > .drop_design, > .drop_content {\n      border-radius: 4px;\n      border-width: 1px;\n      border-style: solid;\n      border-color: transparent;\n    }\n  }\n  // option\n  .drop--light();\n  .drop--left();\n  .drop--bottom();\n}\n\n// addons\n\n.drop--disable {\n  // disable custom animation\n  > .drop > .drop_inner {\n    > .drop_content {\n      opacity: 1 !important;\n      .trans-anim-none() !important;\n    }\n  }\n}\n\n.drop--overflow-y {\n  > .drop_inner > .drop_content {\n    overflow-y: auto;\n    max-height: 25rem;\n    .overflow-style();\n  }\n}\n\n// variant\n\n.drop--light {\n  > .drop_inner {\n    color: @text;\n    > .drop_design {\n      border-color: @gray--300;\n      background: @white;\n      box-shadow: @shadow--light;\n    }\n    > .drop_content > .list--drop {\n      .list-btn({ .btn--text() });\n    }\n  }\n}\n\n.drop--dark {\n  > .drop_inner {\n    .text--inverse();\n    > .drop_design {\n      border-color: @gray--900;\n      background: @gray--900;\n      box-shadow: @shadow--dark;\n    }\n    > .drop_content > .list--drop {\n      .list-btn({ .btn--text() });\n    }\n  }\n}\n\n// animation\n\n.drop_outer {\n  &, > .drop {\n    animation-duration: @time--medium + @time--small; // needed for .out animation timing, put max animation time\n  }\n  &.active {\n    > .drop > .drop_inner {\n      > .drop_design {\n        .anim-zoom.active();\n      }\n      > .drop_content {\n        .anim-y.active();\n        animation-delay: @time--small;\n      }\n    }\n    > .backdrop {\n      .backdrop.active();\n    }\n  }\n  &.out {\n    > .drop > .drop_inner {\n      > .drop_design {\n        .anim-zoom.out();\n        animation-delay: @time--small;\n      }\n      > .drop_content {\n        .anim-y.out();\n      }\n    }\n    > .backdrop {\n      .backdrop.out();\n    }\n  }\n}\n"