export default "@import '~xtend-library/src/core/card/card-setup.less';\n\n/**\n * card\n */\n\n.card {\n  // styles\n  overflow: hidden;\n  border-radius: .75rem;\n  > .card_content {\n    border-radius: inherit;\n  }\n  > .card_design {\n    border-radius: inherit;\n    border-width: 0;\n    border-style: solid;\n    border-color: transparent;\n  }\n  // option\n  .card--light();\n  .card--big();\n}\n\n// addons\n\n.card--max-height {\n  > .card_content {\n    overflow-y: auto;\n    max-height: 25rem;\n    .overflow-style();\n  }\n}\n\n.card--max-width {\n  width: auto;\n  > .card_content {\n    overflow-x: auto;\n    max-width: 35rem;\n    .overflow-style();\n  }\n}\n\n// variant\n\n.card--dark {\n  .text--inverse();\n  > .card_design {\n    border-color: @gray--900;\n    background: linear-gradient(135deg, @grad--dark);\n  }\n}\n\n.card--light {\n  color: @text;\n  > .card_design {\n    border-color: @gray--300;\n    background: linear-gradient(135deg, @grad--light);\n  }\n}\n\n// size\n\n.card--small {\n  > .card_content {\n    padding: .5em 1em;\n    font-size: 1.3rem;\n  }\n}\n\n.card--medium {\n  > .card_content {\n    padding: 1em 1.5em;\n    font-size: 1.4rem;\n  }\n}\n\n.card--big {\n  > .card_content {\n    padding: 1.5em 2em;\n    font-size: 1.4rem;\n  }\n}\n\n.card--giant {\n  > .card_content {\n    padding: 4em 4em;\n    font-size: 1.4rem;\n  }\n}\n\n// special\n\n.card--nodesign {\n  > .card_design {\n    display: none;\n  }\n}\n\n.card--squared {\n  > .card_design, > .card_content {\n    border-radius: 0;\n  }\n}\n\n.card--noborder {\n  > .card_design {\n    border: 0;\n  }\n}\n\n.card--tall {\n  > .card_content {\n    padding-top: 1.75em;\n    padding-bottom: 1.75em;\n  }\n}\n\n.card--short {\n  > .card_content {\n    padding-top: .75em;\n    padding-bottom: .75em;\n  }\n}\n\n.card--wide {\n  > .card_content {\n    padding-left: 2em;\n    padding-right: 2em;\n  }\n}\n\n.card--narrow {\n  > .card_content {\n    padding-left: 1em;\n    padding-right: 1em;\n  }\n}\n\n.card--none {\n  > .card_content {\n    padding: 0;\n  }\n}\n\n.card--none-x {\n  > .card_content {\n    padding-left: 0;\n    padding-right: 0;\n  }\n}\n\n.card--none-y {\n  > .card_content {\n    padding-top: 0;\n    padding-bottom: 0;\n  }\n}\n"