export default "/**\n * loader\n */\n\n.loader {\n  position: absolute !important;\n  z-index: @index--loader;\n  top: 0;\n  left: 0;\n  bottom: 0;\n  right: 0;\n  border-radius: inherit;\n}\n\n.loader--mouse {\n  pointer-events: none;\n  position: fixed !important;\n  top: 0;\n  left: 0;\n  bottom: auto;\n  right: auto;\n  .filler, .spinner {\n    display: block;\n    position: relative;\n  }\n}\n\n// filler\n\n.filler {\n  position: absolute;\n  top: 0;\n  left: 0;\n  bottom: 0;\n  right: 0;\n  margin: auto;\n  span {\n    position: absolute;\n    top: 0;\n    left: 0;\n  }\n}\n\n// spinner\n\n.spinner {\n  position: absolute;\n  top: 0;\n  left: 0;\n  bottom: 0;\n  right: 0;\n  margin: auto;\n}\n\n// direction\n\n.loader--x {\n  .filler {\n    span {\n      width: 100%;\n      height: 100%;\n    }\n  }\n}\n\n.loader--y {\n  .filler {\n    span {\n      width: 100%;\n      height: 100%;\n    }\n  }\n}\n\n// size\n\n.loader--size-top {\n  &:not(.loader--background) {\n    bottom: auto;\n  }\n  .filler {\n    bottom: auto;\n    span {\n      top: 0;\n    }\n  }\n}\n\n.loader--size-bottom {\n  &:not(.loader--background) {\n    top: auto;\n  }\n  .filler {\n    top: auto;\n    span {\n      bottom: 0;\n    }\n  }\n}\n\n.loader--size-left {\n  &:not(.loader--background) {\n    right: auto;\n  }\n  .filler {\n    right: auto;\n    span {\n      left: 0;\n    }\n  }\n}\n\n.loader--size-right {\n  &:not(.loader--background) {\n    left: auto;\n  }\n  .filler {\n    left: auto;\n    span {\n      right: 0;\n    }\n  }\n}\n"