export default "/**\n * tables\n */\n\n// .table\n\n.table {\n  border-collapse: collapse;\n  width: 100%;\n  th {\n    text-align: left;\n    vertical-align: bottom;\n  }\n  td {\n    text-align: left;\n  }\n}\n\n// .table--middle\n\n.table--middle {\n  th, td {\n    vertical-align: middle;\n  }\n}\n\n// .table--center\n\n.table--center {\n  th, td {\n    text-align: center;\n  }\n}\n\n// .table--right\n\n.table--right {\n  th:not(:nth-child(1)), td:not(:nth-child(1)) {\n    text-align: right;\n  }\n}\n\n// .table--scroll\n\n.table--scroll {\n  .block();\n  overflow-x: auto;\n  .overflow-style();\n}\n\n// generate-container-responsive\n\n.generate-table-scroll-responsive();\n.generate-table-scroll-responsive(@i: length(@breakpoints)) when (@i > 0) {\n  .generate-table-scroll-responsive(@i - 1);\n  @breakpoint: extract(@breakpoints, @i);\n  @query-max: 'max-@{breakpoint}';\n  @media @@query-max {\n    .table--scroll-@{breakpoint} {\n      .table--scroll();\n    }\n  }\n}\n"