export default "@import '~xtend-library/src/core/typography/typography-setup.less';\n\n/**\n * font face\n */\n\n@import (css) url(https://fonts.googleapis.com/css?family=Open+Sans:400,600);\n\n.font-sans--regular() {\n  font-family: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;\n  font-weight: 400;\n  font-style: normal;\n}\n\n.font-sans--semibold() {\n  font-family: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;\n  font-weight: 600;\n  font-style: normal;\n}\n\n.font-monospace() {\n  font-family: monaco, Consolas, 'Lucida Console', monospace;\n  font-weight: normal;\n  font-style: normal;\n}\n\n/**\n * rem\n */\n\n// rem\n\nhtml {\n  // base font size for rem\n  // 100% is 16px\n  // value(%) = desired(px) * 100 / 16\n  font-size: 56.25%; // 9px\n  @media @min-lg {\n    font-size: 62.5%; // 10px\n  }\n}\n\nbody {\n  .font-sans--regular();\n  font-size: 1.5rem;\n  color: @text;\n  line-height: 1.8;\n  font-variant-ligatures: none;\n}\n\n/**\n * spacing\n */\n\n.spacing--small {\n  .margin(top, medium) !important;\n  .margin(bottom, small) !important;\n  .margin(y, layout) !important;\n}\n\n.spacing--medium {\n  .margin(top, big) !important;\n  .margin(bottom, medium) !important;\n  .margin(y, layout) !important;\n}\n\n/**\n * header\n */\n\nh1, .h1 {\n  .spacing--medium();\n  .font-sans--semibold();\n  font-size: 4rem;\n  line-height: 1;\n  letter-spacing: 0;\n  text-transform: none;\n}\n\nh2, .h2 {\n  .spacing--medium();\n  .font-sans--semibold();\n  font-size: 3.5rem;\n  line-height: 1.2;\n  letter-spacing: 0;\n  text-transform: none;\n}\n\nh3, .h3 {\n  .spacing--medium();\n  .font-sans--semibold();\n  font-size: 2.5rem;\n  line-height: 1.2;\n  letter-spacing: 0;\n  text-transform: none;\n}\n\nh4, .h4 {\n  .spacing--medium();\n  .font-sans--semibold();\n  font-size: 2rem;\n  line-height: 1.2;\n  letter-spacing: 0;\n  text-transform: none;\n}\n\nh5, .h5 {\n  .spacing--medium();\n  .font-sans--semibold();\n  font-size: 1.7rem;\n  line-height: 1.2;\n  letter-spacing: 0;\n  text-transform: none;\n}\n\nh6, .h6 {\n  .spacing--medium();\n  font-size: 1.3rem;\n  line-height: 1.4;\n  letter-spacing: .05em;\n  text-transform: uppercase;\n}\n\n// special\n\n.header--block {\n  .spacing--medium();\n  .padding(y, small);\n  .padding(x, medium);\n  background: @gray--200;\n  text-align: center;\n}\n\n.header--line {\n  .spacing--medium();\n  .padding(bottom, small);\n  border-bottom: 1px solid @gray--200;\n}\n\n/*\n.header--line {\n  display: flex;\n  position: relative;\n  flex-wrap: nowrap;\n  align-items: center;\n  .margin(bottom, big) !important;\n  .padding(bottom, 3rem);\n  // line\n  &:after {\n    content: '';\n    position: absolute;\n    left: 0;\n    bottom: 0;\n    width: 100%;\n    height: 2px;\n    background: @gray--250;\n  }\n  // spacing\n  > * + * {\n    .margin(left, small);\n  }\n}\n*/\n\n/**\n * text\n */\n\np {\n  .margin(bottom, small);\n  .margin(bottom, layout);\n}\n\nstrong {\n  .font-sans--semibold();\n}\n\nsmall {\n  font-size: .8em;\n}\n\n.muted {\n  opacity: .5;\n}\n\n.font--small {\n  font-size: 1.3rem;\n  line-height: 1.6;\n}\n\n.font--medium {\n  font-size: 1.5rem;\n  line-height: 1.6;\n}\n\n.font--giant {\n  font-size: 1.7rem;\n  line-height: 1.6;\n}\n\n// code\n\ncode {\n  margin: -0.1em 0.1em;\n  padding: 0.1em .3em;\n  background: @gray--200 !important;\n  .font-monospace();\n  font-size: .9em;\n  color: @text;\n}\n\npre {\n  overflow-x: hidden !important;\n  overflow-y: auto !important;\n  .overflow-style(@mode: inner);\n  .margin(y, medium) !important;\n  .margin(y, layout) !important;\n  .padding(all, medium) !important;\n  width: 100%;\n  border-radius: .5rem;\n  border-width: 0;\n  border-style: solid;\n  background: @gray--200 !important;\n  line-height: 0 !important;\n  code {\n    width: 100%;\n    .font-monospace();\n    font-size: 12px !important;\n    line-height: 1.6 !important;\n    white-space: pre-wrap !important;\n  }\n}\n\n// text--inverse\n\n.text--inverse {\n  color: @text--inverse;\n  h1, h2, h3, h4, h5, h6, div {\n    color: inherit;\n  }\n}\n\n/**\n * ul ol\n */\n\nul, ol {\n  .margin(top, none);\n  .margin(bottom, small);\n  .margin(y, layout);\n  padding-left: 2rem;\n  > li {\n    .margin(bottom, small);\n  }\n  > ul, > ol {\n    .margin(y, small);\n  }\n}\n\nul {\n  list-style: disc;\n  ul {\n    list-style: circle;\n  }\n}\n\nol {\n  list-style-type: decimal;\n  ol {\n    list-style-type: lower-alpha;\n  }\n}\n\n// ul list-style custom\n\n/*\nul {\n  list-style: none;\n  padding-left: 0;\n  > li {\n    padding-left: 42px;\n    background-image: url('../../assets/images/list-style-image-squared.svg');\n    background-size: 24px auto;\n    background-position: left 6px;\n    background-repeat: no-repeat;\n  }\n}\n*/\n\n// .list-reset\n// use this mixin to reset styles on xtend ul.list ul.slides_inner etc..\n\n.list-reset() {\n  cursor: default;\n  list-style: none;\n  margin: 0;\n  padding: 0;\n  > li {\n    margin: 0;\n    padding: 0;\n    background: none;\n  }\n}\n\n/**\n * dl\n */\n\ndl {\n  .margin(bottom, small);\n  .margin(bottom, layout);\n  .dl--block();\n  line-height: 1.6;\n  dt {\n    .font-sans--semibold();\n  }\n  dd {\n    .font-sans--regular();\n  }\n}\n\n// .dl--block\n\n.dl--block {\n  dt {\n    .padding(top, small);\n  }\n  dd {\n    .padding(top, tiny);\n  }\n}\n\n// .dl--line\n\n.dl--line {\n  dt {\n    .padding(top, tiny);\n  }\n  dd {\n    .padding(left, small);\n    .padding(top, tiny);\n  }\n}\n\n// .dl--horizontal\n\n.dl--horizontal {\n  text-align: left;\n  dt {\n    .padding(top, tiny);\n    .padding(right, small);\n    float: left;\n    clear: left;\n    width: 80px;\n  }\n  dd {\n    .padding(left, small);\n    .padding(top, tiny);\n    margin-left: 80px;\n  }\n}\n"