export default "/**\n * delay\n */\n\n/* USAGE:\nanimation-delay: 150ms; // @start + @child * @increment\ntransition-delay: 150ms; // @start + @child * @increment\n.nth-child(@child:6, @start: 0ms, @increment: 25ms, @decrement: 0ms, {\n  .optional-nested {\n    @animation-delay();\n    @transition-delay();\n  }\n});\nanimation-delay: 0ms; // @start - @child * @decrement\ntransition-delay: 0ms; // @start - @child * @decrement\n.nth-child(@child:6, @start: 150ms, @increment: 0ms, @decrement: 25ms, {\n  .optional-nested {\n    @animation-delay();\n    @transition-delay();\n  }\n});\n*/\n\n.nth-child(@child, @start, @increment, @decrement, @rules) when (@child > 0) {\n  .nth-child(@child - 1, @start, @increment, @decrement, @rules);\n  &:nth-child(@{child}) {\n    @rules();\n  }\n}\n\n@animation-delay: { animation-delay: @start + ((@child - 1) * @increment) - ((@child - 1) * @decrement); };\n\n@transition-delay: { transition-delay: @start + ((@child - 1) * @increment) - ((@child - 1) * @decrement); };\n\n/**\n * custom\n */\n\n// collapse\n\n.collapse--height {\n  overflow: hidden;\n  height: 0;\n  &:before {\n    display: none;\n    content: 'xt-collapse--height';\n  }\n}\n\n.collapse--width {\n  overflow: hidden;\n  width: 0;\n  &:after {\n    display: none;\n    content: 'xt-collapse--width';\n  }\n}\n"