export default "@import '~xtend-library/src/core/list/list-setup.less';\n\n/**\n * vars\n */\n\n// generate class: [false], [true] non responsive, [sm, md, lg] responsive breakpoints\n\n@generate-class--list: true;\n\n// space\n\n@list-spaces: none, micro, tiny, small, medium, big, giant; // if you change this change also variables names below\n@list-space--none: 0 0; // vertical horizontal\n@list-space--micro: .25rem .25rem;\n@list-space--tiny: .5rem .5rem;\n@list-space--small: .75rem .75rem;\n@list-space--medium: 1rem 1rem;\n@list-space--big: 1.5rem 1.5rem;\n@list-space--giant: 2rem 2rem;\n\n/**\n * list\n */\n\n.list {\n  // option\n  .list-space(small);\n  .list-btn({\n    .btn();\n  });\n}\n\n.list-block {\n  // option\n  .list-space(small);\n  .list-btn({\n    .btn();\n  });\n}\n\n// addons\n\n.list--drop {\n  flex-wrap: nowrap;\n  flex-direction: column;\n  .list-space(none); // space\n  .list-btn({\n    .btn--small();\n    .btn--squared();\n    padding-left: 2.2rem;\n    padding-right: 2.2rem;\n    white-space: nowrap;\n  }); // all buttons\n}\n\n// variant\n\n/*\n.list--secondary {\n  .list-btn({ .btn--secondary(); });\n}\n*/\n\n// size\n\n/*\n.list--small {\n  .list-btn({ .btn--small(); });\n}\n*/\n\n// special\n\n/*\n.list--wide {\n  .list-btn({ .btn--wide(); });\n}\n*/\n"