export default "/**\n * xtend extension: overlay-position\n */\n\n.overlay-position {\n  .overlay {\n    position: relative;\n    z-index: @index--overlay;\n    .padding(all, none) !important;\n    .overlay_inner {\n      position: static;\n    }\n  }\n  .overlay_inner {\n    .flex--auto();\n    display: flex;\n    flex-direction: column;\n  }\n  .overlay-position--left& {\n    .overlay {\n      margin-left: 0;\n      margin-right: auto;\n    }\n  }\n  .overlay-position--right& {\n    .overlay {\n      margin-left: auto;\n      margin-right: 0;\n    }\n  }\n}\n"