export default "/**\n * demo\n */\n\nhtml.iframe-full {\n  body {\n    overflow: hidden !important;\n  }\n}\n\nhtml.iframe-inside {\n  body {\n    .overflow-style(@mode: inner);\n    background: @white !important;\n  }\n}\n\n#body-inner {\n  position: relative;\n}\n\n// .demo-cols\n\n.demo-cols {\n  [class^='col-'], [class*=' col-'], [class^='demo--col-'], [class*=' demo--col-'] {\n    &:before {\n      position: relative;\n      float: left;\n      width: 100%;\n      height: 22px;\n      min-height: 100%;\n      border: 1px solid darken(@secondary, 5%);\n      background: @secondary;\n      content: attr(data-index);\n      .font-sans--semibold();\n      font-size: 12px;\n      color: @primary;\n      line-height: 1.8;\n      text-align: center;\n    }\n  }\n}\n\n.demo-cols-nested {\n  [class^='col-'], [class*=' col-'] {\n    .demo-cols();\n  }\n}\n\n/**\n * demo\n */\n\n@demo-height: 35rem;\n\npre {\n  max-height: @demo-height;\n}\n\n// .demo\n\n.demo {\n  .margin(y, layout) !important;\n  .margin(top, medium);\n  .margin(bottom, big);\n  border-radius: .5rem;\n  border-width: 2px;\n  border-style: solid;\n  border-color: @gray--200;\n  .demo_tabs {\n    .margin-remove(top, 2px); // remove border space\n    border-top-left-radius: 4px;\n    border-top-right-radius: 4px;\n  }\n  // code\n  .demo_item {\n    position: relative;\n    .toggle--block();\n    .demo_code {\n      position: absolute;\n      z-index: 4;\n      width: 100%;\n      .demo_code_inner {\n        display: flex;\n        flex-flow: column;\n        height: 100%;\n      }\n      &.active {\n        z-index: 5;\n      }\n    }\n    &[data-iframe] {\n      &:not(.loaded) {\n        .demo_item_wrapper {\n          position: relative;\n          height: @demo-height;\n        }\n        iframe {\n          display: none;\n        }\n      }\n      &.loaded {\n        .demo_item_wrapper > .loader {\n          display: none;\n        }\n      }\n      iframe {\n        display: block;\n        width: 100%;\n        height: 0;\n        border: 0;\n        &:not(.iframe-full) {\n          height: @demo-height;\n        }\n      }\n    }\n    &[data-shadow] {\n      &:not(.loaded) {\n        .demo_item_wrapper {\n          height: @demo-height;\n        }\n        .demo_shadow {\n          display: none;\n        }\n      }\n      &.loaded {\n        .demo_item_wrapper + .loader {\n          display: none;\n        }\n      }\n    }\n    &:not(.populated) {\n      .demo_code_body {\n        width: 100%;\n        overflow: hidden;\n      }\n    }\n    &.populated {\n      .demo_code_body_item {\n        .toggle--block();\n      }\n    }\n  }\n  .hljs {\n    z-index: 1;\n    .padding(top, small) !important;\n  }\n  .demo_code_body {\n    z-index: 1;\n    flex: 1;\n  }\n  // tabs\n  .demo_tabs, .demo_code_tabs, .demo_code_body {\n    background: @gray--200;\n  }\n  .demo_tabs, .demo_code_tabs {\n    position: relative;\n    z-index: 2;\n    display: flex;\n    .justify-content(space-between);\n    > * {\n      .list();\n      .list-space(tiny);\n    }\n    .padding(y, small);\n    .padding(x, small);\n    .btn {\n      // animation\n      &:hover, &.hover, &:active, &.active {\n        border-color: @white;\n        background: @white;\n        color: @text;\n      }\n    }\n  }\n  .demo_tabs_inner {\n    margin-top: -3px;\n  }\n  // text\n  .demo_text {\n    .alert();\n    .alert--small();\n    .margin(all, none);\n    .margin-remove(top, 2px); // remove border space\n    border-bottom-left-radius: 0;\n    border-bottom-right-radius: 0;\n  }\n  // .demo_source\n  .demo_source {\n    .padding(all, medium);\n  }\n  .demo_source {\n    &:not(.demo_source--from) {\n      display: none;\n    }\n  }\n}\n"