export default "/**\n * xtend extension: overlay-message-outside\n */\n\n.overlay-message-outside {\n  // styles\n  position: fixed;\n  z-index: @index--overlay !important;\n  top: 50%;\n  width: calc(100vw - 510px);\n  text-align: center;\n  @media @max-sm {\n    display: none;\n  }\n  .overlay-position--left & {\n    right: 0;\n  }\n  .overlay-position--right & {\n    left: 0;\n  }\n  // animation\n  .overlay-position--left.active & {\n    .anim-x-inverse.active();\n  }\n  .overlay-position--left.out & {\n    .anim-x.out();\n  }\n  .overlay-position--right.active & {\n    .anim-x.active();\n  }\n  .overlay-position--right.out & {\n    .anim-x-inverse.out();\n  }\n}\n"