export default "@import '~xtend-library/src/core/grid/grid-setup.less';\n\n/**\n * vars\n */\n\n// generate class: [false], [true] non responsive, [sm, md, lg] responsive breakpoints\n\n@generate-class--grid: sm, md, lg;\n@generate-class--row: true;\n\n// container\n\n@container--max: 1340px; // max-width (none for full width)\n@container--space: none 1.5rem, xs 1.5rem, sm 3rem, md 3rem, lg 3rem, xl 3rem; // container and overlay spacing\n\n// column\n\n@columns: 12;\n\n// row\n\n@row-spaces: none, micro, tiny, small, medium, big, giant; // if you change this change also variables names below\n@row-space--none: 0 0; // vertical horizontal\n@row-space--micro: .25rem .25rem;\n@row-space--tiny: .5rem .5rem;\n@row-space--small: .75rem .75rem;\n@row-space--medium: 1rem 1rem;\n@row-space--big: 1.5rem 1.5rem;\n@row-space--giant: 2rem 2rem;\n\n/**\n * row\n */\n\n.row {\n  // option\n  .row-space(big);\n  flex-wrap: wrap;\n  flex-direction: row;\n}\n"