export default "/**\n * xtend extension: alert\n */\n\n@alert--design: ~':before'; // selector for design\n\n.alert {\n  display: flex;\n  flex-direction: column;\n  .flex--auto();\n  width: 100%;\n  position: relative;\n  z-index: @index--base; // fix :before negative z-index\n  // styles\n  .margin(y, medium);\n  border-radius: 4px;\n  &@{alert--design} {\n    .design-setup();\n    border-radius: inherit;\n    border-width: 3px;\n    border-style: solid;\n    border-color: transparent;\n  }\n  // option\n  .alert--light();\n  .alert--medium();\n  // animation\n  &.active {\n    .anim-x.active();\n    &.inverse {\n      .anim-x-inverse.active();\n    }\n  }\n  &.out {\n    .anim-x.out();\n    &.inverse {\n      .anim-x-inverse.out();\n    }\n  }\n}\n\n// variant\n\n.alert--dark {\n  .text--inverse();\n  &@{alert--design} {\n    border-color: @gray--900;\n    background: @gray--900;\n  }\n}\n\n.alert--light {\n  color: @text;\n  &@{alert--design} {\n    border-color: @gray--200;\n    background: @gray--200;\n  }\n}\n\n// size\n\n.alert--small {\n  > .alert_content {\n    padding: .5em 1em;\n    font-size: 1.3rem;\n  }\n}\n\n.alert--medium {\n  > .alert_content {\n    padding: 1em 1.5em;\n    font-size: 1.4rem;\n  }\n}\n"