export default "/**\n * structure\n */\n\n// indexes\n\n.site_header {\n  position: relative;\n  z-index: 9;\n}\n\n.site_main {\n  position: relative;\n  z-index: 8;\n}\n\n.site_footer {\n  position: relative;\n  z-index: 8;\n}\n\n// prevent horizontal overflow\n\n.site_wrapper {\n  overflow: hidden;\n}\n\n.site_header, .site_main, .site_footer {\n  .block();\n}\n\n// sticky footer\n\n.site_wrapper {\n  display: flex;\n  flex-wrap: wrap;\n  flex-direction: column;\n  min-height: 100vh;\n}\n\n.site_main {\n  .flex--auto();\n}\n\n/**\n * article\n */\n\n.site_article {\n  background: @white;\n  box-shadow: @shadow--light;\n}\n\n.site_article, .site_footer_bottom {\n  .site_wrapper--with-sidebar & {\n    @media @min-md {\n      margin-left: @site_article_sidebar_width;\n    }\n  }\n  .site_wrapper--with-aside & {\n    @media @min-lg {\n      margin-right: @site_article_aside_width;\n    }\n  }\n}\n\n.site_article_hero {\n  .padding(y, giant);\n  .padding(x, medium, sm);\n  background: @gray--200;\n  p {\n    .font--giant();\n  }\n}\n\n.site_article_content {\n  .padding(y, giant);\n  .padding(x, medium, sm);\n}\n\n.site_article_foot {\n  .padding(y, giant);\n  .padding(x, medium, sm);\n  background: @gray--200;\n}\n\n/**\n * sidebar\n */\n\n// .site_article_sidebar\n\n@site_article_sidebar_width: 25rem;\n\n.site_article_sidebar {\n  position: fixed;\n  z-index: 2;\n  top: @site_header_height;\n  left: 0;\n  bottom: 0;\n  @media @max-md {\n    .sticky--disable();\n  }\n  @media @min-md {\n    width: @site_article_sidebar_width;\n  }\n  overflow-x: hidden;\n  overflow-y: auto;\n  .overflow-style(@mode: inner) !important;\n  line-height: 1.5;\n}\n\n.site_article_sidebar_inner {\n  .padding(y, giant);\n}\n\n.site_article_sidebar_line {\n  position: absolute;\n  top: .5rem;\n  left: 0;\n  bottom: .5rem;\n  width: 1px !important;\n  background: @gray--300;\n}\n\n.site_article_sidebar_cat {\n  display: flex;\n  flex-direction: column;\n  align-items: flex-start;\n  justify-content: flex-start;\n}\n\n.site_article_sidebar_sub, .site_article_sidebar_adiacent {\n  display: none;\n  width: 100%;\n  //.padding(y, .25rem);\n  .site_article_sidebar_item {\n    position: relative;\n    display: flex;\n    flex-direction: column;\n    align-items: flex-start;\n    justify-content: flex-start;\n    .padding(left, 1.25rem);\n    > * {\n      width: 100%;\n    }\n  }\n  &.active {\n    display: block;\n  }\n}\n\n// .btn--site_article_sidebar\n\n.btn--site_article_sidebar {\n  .block();\n  .btn--left();\n  .link--none();\n  .btn--squared();\n  .btn--noborder();\n  .btn--nodesign();\n  .btn--noeffect();\n  .font-sans--regular();\n  color: @text;\n  text-transform: none;\n  // states\n  .transition-none() !important;\n  &:before {\n    content: '';\n    position: absolute;\n    margin-left: -1.25rem;\n    width: 0;\n    height: 11px;\n    background: linear-gradient(0deg, @grad--primary);\n    box-shadow: @shadow--primary;\n    transition-property: all;\n    transition-duration: @time--small;\n    transition-timing-function: @bez--out;\n  }\n  &:hover, &.hover {\n    &:before {\n      width: 4px !important;\n      transition-property: all;\n      transition-duration: @time--tiny;\n      transition-timing-function: @bez--in;\n    }\n  }\n  &:active, &.active {\n    .font-sans--semibold();\n    color: @black;\n    &:before {\n      width: 1px;\n      transition-property: all;\n      transition-duration: @time--small;\n      transition-timing-function: @bez--in;\n    }\n  }\n  &.current {\n    .font-sans--semibold();\n    color: @black;\n    &:before {\n      width: 1px;\n      transition-property: all;\n      transition-duration: @time--small;\n      transition-timing-function: @bez--in;\n    }\n  }\n  // custom\n  &.btn--site_article_sidebar--cat {\n    padding: 10px 0 7px 0 !important;\n    .font-sans--semibold();\n    font-size: 12px;\n    text-transform: uppercase;\n    &:before {\n      display: none;\n    }\n  }\n  &.btn--site_article_sidebar--sub {\n    padding: 4px 0 !important;\n    font-size: 13px;\n    &:before {\n      margin-top: 2px;\n    }\n  }\n  &.btn--site_article_sidebar--adiacent {\n    padding: 4px 0 !important;\n    font-size: 12px;\n    &:before {\n      margin-top: 1px;\n    }\n  }\n}\n\n/**\n * aside\n */\n\n// .site_article_aside\n\n@site_article_aside_width: 25rem;\n\n.site_article_aside {\n  position: fixed;\n  z-index: 1;\n  top: @site_header_height;\n  right: 0;\n  bottom: 0;\n  @media @max-lg {\n    .sticky--disable();\n  }\n  @media @min-lg {\n    width: @site_article_aside_width;\n  }\n}\n\n.site_article_aside_inner {\n  .padding(y, giant);\n  text-align: right;\n}\n\n/**\n * foot\n */\n\n.article_foot {\n  display: flex;\n  justify-content: space-between;\n}\n"