export default "/**\n * header\n */\n\n@site_header_height: 5.5rem;\n\n.site_header_top_left {\n  width: @site_article_sidebar_width;\n}\n\n.site_header_top_center {\n  .flex--auto();\n}\n\n.site_header_top_right {\n  width: @site_article_aside_width;\n}\n\n// .site_header\n\n.site_header_top {\n  display: flex;\n  color: @white;\n  background: linear-gradient(0deg, @grad--primary);\n}\n\n.site_header_top_row {\n  .flex--auto();\n  align-items: center;\n  justify-content: space-between;\n}\n\n.site_header_top_links_outer {\n  align-items: center;\n  justify-content: space-between;\n}\n\n.site_header_top_links {\n  .list();\n}\n\n.site_header_top_search {\n  .form-item {\n    // styles\n    border-radius: 0;\n    border-width: 0;\n    padding: .5em 1em; // size in em inherit .form-group size\n    .font-sans--semibold();\n    font-size: 14px;\n    line-height: 1.2;\n    text-transform: uppercase;\n    // styles\n    background: fade(@gray--900, 10%);\n    color: @text--inverse;\n    &:focus, &.active {\n      background: fade(@gray--900, 15%);\n    }\n    &:-ms-input-placeholder {\n      color: @text--inverse;\n    }\n    &:-moz-placeholder {\n      color: @text--inverse;\n    }\n    &::-webkit-input-placeholder {\n      color: @text--inverse;\n    }\n    &::-ms-input-placeholder {\n      color: @text--inverse;\n    }\n    &::-moz-placeholder {\n      color: @text--inverse;\n    }\n    &::placeholder {\n      color: @text--inverse;\n    }\n  }\n}\n\n.site_header_top_social {\n  float: right;\n  .list();\n  .list-space(none);\n}\n\n// .btn--site_header_top_link\n\n.btn--site_header_top_link {\n  .link--none();\n  .btn--squared();\n  .btn--noborder();\n  .btn--noeffect();\n  padding: .3em .5em;\n  background: transparent;\n  .font-sans--semibold();\n  font-size: 14px;\n  color: @text--inverse;\n  line-height: 1;\n  text-transform: uppercase;\n  &:hover, &.hover, &:active, &.active, &.current {\n    background: fade(@gray--900, 10%);\n    color: @text--inverse;\n  }\n}\n\n// .btn--site_header_top_social\n\n.btn--site_header_top_social {\n  .link--none();\n  .btn--nodesign();\n  .btn--noeffect();\n  padding: .3em .5em;\n  border-radius: 0;\n  color: @text--inverse;\n  line-height: 1;\n  text-transform: uppercase;\n  opacity: 0.8;\n  &:hover, &.hover, &:active, &.active {\n    color: @text--inverse;\n    opacity: 1;\n  }\n  // effect\n  &:active, &.active {\n    transform: scale(1.2);\n  }\n}\n"