export default "/**\n * footer\n */\n\n// .site_footer_bottom\n\n.site_footer_bottom {\n  .padding(top, medium);\n  .padding(bottom, medium);\n  font-size: 12px;\n  .separator {\n    vertical-align: text-top;\n  }\n  a {\n    .font-sans--semibold();\n    color: @black;\n    text-decoration: none;\n    // animation\n    &:hover, &.hover, &:active, &.active {\n      color: @black;\n      text-decoration: underline;\n    }\n  }\n}\n\n.site_footer_bottom_row {\n  .flex--auto();\n  @media @max-sm {\n    .row-space(tiny);\n    flex-direction: column;\n    justify-content: center;\n    line-height: 1.8;\n    text-align: center;\n  }\n  @media @min-sm {\n    .row-space(small);\n    justify-content: space-between;\n  }\n}\n\n.site_footer_bottom_right {\n  @media @min-sm {\n    margin-left: auto;\n  }\n}\n"