export default "@import '~xtend-library/src/core/button/button-setup.less';\n\n/**\n * button\n */\n\n.btn {\n  // styles\n  overflow: hidden;\n  border-radius: 4px;\n  border-width: 1px;\n  border-style: solid;\n  border-color: transparent;\n  .font-sans--semibold();\n  line-height: 1.2;\n  letter-spacing: 0;\n  text-transform: uppercase;\n  // option\n  .btn--secondary();\n  .btn--medium();\n}\n\n// animation\n\n.btn {\n  // transition\n  transition-property: all;\n  transition-duration: @time--big;\n  transition-timing-function: @bez--out;\n  &:hover, &.hover, &:active, &.active {\n    transition-property: all;\n    transition-duration: @time--big;\n    transition-timing-function: @bez--in;\n  }\n}\n\n// variant\n\n.btn--primary {\n  .link--none();\n  border-color: @primary;\n  background: @primary;\n  color: @text--inverse;\n  &:hover, &.hover, &:active, &.active {\n    border-color: @primary--dark;\n    background: @primary--dark;\n    color: @text--inverse--dark;\n  }\n  // effect\n  &:active, &.active {\n    box-shadow: @shadow--primary;\n  }\n}\n\n.btn--secondary {\n  .link--none();\n  border-color: @secondary;\n  background: @secondary;\n  color: @text;\n  &:hover, &.hover, &:active, &.active {\n    border-color: @secondary--dark;\n    background: @secondary--dark;\n    color: @text--dark;\n  }\n  // effect\n  &:active, &.active {\n    box-shadow: @shadow--secondary;\n  }\n}\n\n.btn--text {\n  .link--none();\n  .btn--noeffect();\n  border-color: transparent;\n  background: transparent;\n  color: inherit;\n  &:hover, &.hover, &:active, &.active {\n    border-color: transparent;\n    background: transparent;\n    color: inherit;\n    opacity: .75;\n  }\n}\n\n.btn--link {\n  .btn--none();\n  .btn--nodesign();\n  .btn--noeffect();\n  // reset\n  font-family: inherit !important;\n  font-style: inherit !important;\n  font-weight: inherit !important;\n  letter-spacing: inherit !important;\n  text-transform: inherit !important;\n}\n\n// size\n\n.btn--tiny {\n  padding: .5em 1em;\n  font-size: 10px;\n}\n\n.btn--small {\n  padding: .6em 1.2em;\n  font-size: 1.2rem;\n}\n\n.btn--medium {\n  padding: .6em 1.2em;\n  font-size: 1.335rem; // @FIX odd rem with .035 added\n}\n\n.btn--big {\n  padding: .6em 1.2em;\n  font-size: 1.4rem;\n}\n\n.btn--giant {\n  padding: .6em 1.2em;\n  font-size: 1.535rem; // @FIX odd rem with .035 added\n}\n\n.btn--huge {\n  padding: .6em 1.2em;\n  font-size: 1.6rem;\n}\n\n// special\n\n.btn--nodesign {\n  border-color: transparent !important;\n  background: transparent !important;\n}\n\n.btn--noeffect {\n  &:active, &.active {\n    box-shadow: none;\n  }\n}\n\n.btn--squared {\n  border-radius: 0;\n}\n\n.btn--noborder {\n  border: 0;\n}\n\n.btn--tall {\n  padding-top: .9em;\n  padding-bottom: .9em;\n}\n\n.btn--short {\n  padding-top: .3em;\n  padding-bottom: .3em;\n}\n\n.btn--wide {\n  padding-left: 1.8em;\n  padding-right: 1.8em;\n}\n\n.btn--narrow {\n  padding-left: .6em;\n  padding-right: .6em;\n}\n\n.btn--none {\n  .btn--noeffect();\n  .btn--squared();\n  padding: 0;\n  border: 0 !important;\n  background: transparent !important;\n}\n\n.btn--none-x {\n  .btn--noeffect();\n  .btn--squared();\n  padding-left: 0;\n  padding-right: 0;\n  border-left: 0 !important;\n  border-right: 0 !important;\n  background: transparent !important;\n}\n\n.btn--none-y {\n  .btn--noeffect();\n  .btn--squared();\n  padding-top: 0;\n  padding-bottom: 0;\n  border-top: 0 !important;\n  border-bottom: 0 !important;\n  background: transparent !important;\n}\n\n.btn--icon {\n  line-height: 1;\n  padding: .8em .8em;\n}\n\n.btn--transform {\n  letter-spacing: 0;\n  text-transform: none;\n}\n\n// custom\n\n.btn--close {\n  .btn--text(); // variant\n  .btn--icon() !important; // special\n  .icon-xt(@content: @icon-xt--close, @size: @icon--medium);\n}\n"