export default "@import '~xtend-library/src/core/form/form-setup.less';\n\n/**\n * vars\n */\n\n@select-arrow--size: 4px; // in px\n@select-space--size: 24px; // in px\n\n/**\n * fieldset\n */\n\nfieldset + fieldset {\n  .margin(top, big);\n}\n\n/**\n * legend\n */\n\nlegend {\n  .block();\n  .h4();\n  .header--line();\n}\n\n/**\n * text\n */\n\n.form-text {\n  .block();\n  .margin(top, small);\n  .font--small();\n  .muted();\n}\n\n/**\n * group\n */\n\n.form-group {\n  // option\n  .form-group.form-group--medium();\n}\n\n// addons\n\n.form-group--row {\n  .row-space--x(medium);\n}\n\n.form-group--row-sm {\n  @media @min-sm {\n    .form-group--row();\n  }\n}\n\n.form-group--horizontal {\n  .row-space--x(medium);\n  align-items: center;\n  text-align: right;\n}\n\n.form-group--horizontal-sm {\n  @media @min-sm {\n    .form-group--horizontal();\n  }\n}\n\n.form-group--inline {\n  .list-space--x(medium);\n  align-items: baseline;\n}\n\n.form-group--inline-sm {\n  @media @min-sm {\n    .form-group--inline();\n  }\n}\n\n// size\n\n.form--tiny {\n  .form-groups({ .form-group.form-group--tiny() });\n}\n\n.form--medium {\n  .form-groups({ .form-group.form-group--medium() });\n}\n\n.form--giant {\n  .form-groups({ .form-group.form-group--giant() });\n}\n\n.form-group.form-group--tiny {\n  .margin(bottom, 1em);\n  font-size: 1.1rem;\n}\n\n.form-group.form-group--medium {\n  .margin(bottom, 1.5em);\n  font-size: 1.3rem;\n}\n\n.form-group.form-group--giant {\n  .margin(bottom, 1.5em);\n  font-size: 1.5rem;\n}\n\n/**\n * label\n */\n\n.form-label {\n  // styles\n  .margin(bottom, 1em); // size in em inherit .form-group size\n  .font-sans--semibold();\n  font-size: 1em; // size in em inherit .form-group size\n  color: @gray--500;\n  line-height: 1.2;\n  letter-spacing: .05em;\n  text-transform: uppercase;\n}\n\n/**\n * item\n */\n\n.form-item {\n  // styles\n  border-radius: 4px;\n  border-width: 1px;\n  border-style: solid;\n  border-color: transparent;\n  padding: .75em 1.2em; // size in em inherit .form-group size\n  .font-sans--regular();\n  font-size: 1em; // size in em inherit .form-group size\n  line-height: 1.4;\n  // option\n  .form-item--light();\n  // animation\n  transition-property: color, border, background-color;\n  transition-duration: @time--medium;\n  transition-timing-function: @bez--out;\n  &:hover {\n    transition-property: color, border, background-color;\n    transition-duration: @time--medium;\n    transition-timing-function: @bez--in;\n  }\n  &:focus, &.active {\n    transition-property: color, border, background-color;\n    transition-duration: @time--medium;\n    transition-timing-function: @bez--in;\n  }\n}\n\n// variant\n\n.form-item--light {\n  // styles\n  border-color: @gray--300;\n  background-color: fade(@gray--300, 15%); // @FIX no background: or it overrides background-image\n  color: @text;\n  &:focus, &.active {\n    border-color: @gray--300;\n    background-color: fade(@gray--300, 30%); // @FIX no background: or it overrides background-image\n  }\n  // select arrow\n  select:not([multiple])& {\n    @select-arrow-color: @text;\n    @select-space-color: @gray--200;\n    background-image: linear-gradient(45deg, transparent 50%, @select-arrow-color 50%), linear-gradient(135deg, @select-arrow-color 50%, transparent 50%), linear-gradient(to right, @select-space-color, @select-space-color);\n  }\n}\n\n// placeholder\n\n:-ms-input-placeholder {\n  color: @gray--500;\n}\n\n:-moz-placeholder {\n  color: @gray--500;\n}\n\n::-webkit-input-placeholder {\n  color: @gray--500;\n}\n\n::-ms-input-placeholder {\n  color: @gray--500;\n}\n\n::-moz-placeholder {\n  color: @gray--500;\n}\n\n::placeholder {\n  color: @gray--500;\n}\n\n/**\n * select[multiple]\n */\n\nselect[multiple]& {\n  padding: 0;\n  option {\n    padding: .5rem 1rem;\n  }\n}\n\n/**\n * check and radio\n */\n\n// label\n\n.checkradio-label {\n  .margin(all, none); // reset .form-label\n  .padding(left, small); // space from checkbox/radio\n  // styles\n  .font-sans--regular();\n  font-size: 1em;\n  line-height: 1.2;\n  letter-spacing: 0;\n  text-transform: none;\n  // disabled\n  &.disabled {\n    opacity: 1;\n    &:before {\n      background: darken(@gray--200, 2.5%) !important;\n      border: 1px solid darken(@gray--200, 2.5%) !important;\n    }\n    &:after {\n      color: darken(@gray--200, 25%) !important;\n    }\n  }\n}\n\n// styled\n\n.checkbox, .radio {\n  padding-left: 3rem;\n  &:before, &:after {\n    width: 1.8rem;\n    height: 1.8rem;\n  }\n  &:before {\n    background: @gray--200;\n    border: 1px solid @gray--300;\n  }\n  &:after {\n    color: @primary;\n  }\n  // animation\n  &:before {\n    transform: scale(.9, .9);\n    transition-property: all;\n    transition-duration: @time--medium;\n    transition-timing-function: @bez--out;\n  }\n  &:after {\n    opacity: 0 !important;\n    transform: scale(.9, .5);\n    transition-property: all;\n    transition-duration: @time--medium;\n    transition-timing-function: @bez--out;\n  }\n  &.focus, &:focus {\n    &:before {\n      border: 1px solid @primary;\n    }\n  }\n  &.active {\n    &:before {\n      transform: scale(1, 1);\n      transition-property: all;\n      transition-duration: @time--medium;\n      transition-timing-function: @bez--in;\n    }\n    &:after {\n      opacity: 1 !important;\n      transform: scale(1, 1);\n      transition-property: all;\n      transition-duration: @time--medium;\n      transition-timing-function: @bez--in;\n    }\n  }\n}\n\n.checkbox {\n  &:before {\n    border-radius: 4px;\n  }\n  &:after {\n    .icon-xt();\n    content: @icon-xt--check;\n    font-size: 100%;\n  }\n}\n\n.radio {\n  &:before {\n    border-radius: 50%;\n  }\n  &:after {\n    .icon-xt();\n    content: @icon-xt--circle;\n    font-size: 50%;\n  }\n}\n\n// pseudo\n\ninput[type=\"checkbox\"]:not(.unstyled) {\n  // focus\n  &:focus ~ .form-label {\n    .checkbox.focus();\n  }\n  // active\n  &:checked ~ .form-label {\n    .checkbox.active();\n  }\n  // disabled\n  &:disabled ~ .form-label {\n    .checkradio-label.disabled();\n  }\n}\n\ninput[type=\"radio\"]:not(.unstyled) {\n  // focus\n  &:focus ~ .form-label {\n    .radio.focus();\n  }\n  // active\n  &:checked ~ .form-label {\n    .radio.active();\n  }\n  // disabled\n  &:disabled ~ .form-label {\n    .checkradio-label.disabled();\n  }\n}\n\n/**\n * validation\n */\n\n.form-item {\n  &:invalid {\n    box-shadow: none !important;\n  }\n}\n"