export default "@import '~xtend-library/src/extension/loader/loader-setup.less';\n\n/**\n * loader\n */\n\n// spinner\n\n.spinner {\n  width: 25px;\n  height: 25px;\n  .btn & {\n    width: 1em;\n    height: 1em;\n  }\n  line-height: 0;\n  svg {\n    filter: drop-shadow(0 .1rem .2rem fade(@black, 10%)) drop-shadow(0 .2rem .4rem fade(@black, 25%));\n    circle {\n      fill: none;\n      stroke-width: 30;\n      transform-origin: center center;\n      .btn & {\n        stroke-width: 40;\n      }\n    }\n    &:nth-child(1) {\n      circle {\n        stroke: @primary;\n        stroke-dashoffset: 628;\n        transform: rotateY(0deg) rotateZ(-90deg);\n        animation: anim-spinner @time--big * 2 infinite @bez--in;\n      }\n    }\n    &:nth-child(2) {\n      position: absolute;\n      top: 0;\n      left: 0;\n      circle {\n        stroke: fade(@primary, 5%);\n        stroke-dashoffset: 0;\n      }\n    }\n  }\n}\n\n@keyframes anim-spinner {\n  0% {\n    stroke-dashoffset: 628;\n    transform: rotateY(0deg) rotateZ(-90deg);\n    // fix no support transform-box: border-box;\n    transform-origin: 48% 48%;\n  }\n  50% {\n    stroke-dashoffset: 0;\n    transform: rotateY(0deg) rotateZ(90deg);\n    animation-timing-function: @bez--out;\n    // fix no support transform-box: border-box;\n    transform-origin: 48% 48%;\n  }\n  50.0001% {\n    stroke-dashoffset: 0;\n    transform: rotateY(180deg) rotateZ(90deg);\n    // fix no support transform-box: border-box;\n    transform-origin: center center;\n  }\n  100% {\n    stroke-dashoffset: 628;\n    transform: rotateY(180deg) rotateZ(-90deg);\n    // fix no support transform-box: border-box;\n    transform-origin: 46% 50%;\n  }\n}\n\n// direction\n\n.loader--x {\n  .filler {\n    span:nth-child(1) {\n      width: 0;\n      background: linear-gradient(90deg, @grad--primary);\n      box-shadow: @shadow--primary;\n      animation: anim-loader--x @time--big * 2 infinite @bez--in;\n    }\n    span:nth-child(2) {\n      background: fade(@primary, 5%);\n    }\n  }\n}\n\n.loader--y {\n  .filler {\n    span:nth-child(1) {\n      height: 0;\n      background: linear-gradient(0deg, @grad--primary);\n      box-shadow: @shadow--primary;\n      animation: anim-loader--y @time--big * 2 infinite @bez--in;\n    }\n    span:nth-child(2) {\n      background: fade(@primary, 5%);\n    }\n  }\n}\n\n@keyframes anim-loader--x {\n  0% {\n    left: 0;\n    width: 0;\n  }\n  50% {\n    left: 0;\n    width: 100%;\n    animation-timing-function: @bez--out;\n  }\n  100% {\n    left: 100%;\n    width: 0;\n  }\n}\n\n@keyframes anim-loader--y {\n  0% {\n    top: 100%;\n    height: 0;\n  }\n  50% {\n    top: 0;\n    height: 100%;\n    animation-timing-function: @bez--out;\n  }\n  100% {\n    top: 0;\n    height: 0;\n  }\n}\n\n// size\n\n.loader--size-x, .loader--size-top, .loader--size-bottom {\n  .filler {\n    height: 3px;\n  }\n}\n\n.loader--size-y, .loader--size-left, .loader--size-right {\n  .filler {\n    width: 3px;\n  }\n}\n\n.loader--size-x {\n  .filler {\n    left: 25%;\n    right: 25%;\n  }\n}\n\n.loader--size-y {\n  .filler {\n    top: 25%;\n    bottom: 25%;\n  }\n}\n\n// background\n\n.loader--background {\n  background: fade(@white, 80%);\n}\n\n// animations\n\n.loader {\n  &.active {\n    opacity: 1;\n    transition-property: opacity;\n    transition-duration: @time--small;\n    transition-timing-function: @bez--in;\n    .spinner, .filler {\n      opacity: 1;\n      transform: scale(1);\n      transition-property: opacity, transform;\n      transition-duration: @time--small;\n      transition-timing-function: @bez--in;\n    }\n  }\n  &.out {\n    opacity: 0;\n    transition-property: opacity;\n    transition-duration: @time--small;\n    transition-timing-function: @bez--out;\n    .spinner, .filler {\n      opacity: 0;\n      transform: scale(.75, .75);\n      transition-property: opacity, transform;\n      transition-duration: @time--small;\n      transition-timing-function: @bez--out;\n    }\n  }\n}\n\n// mouse\n\n.loader--mouse {\n  // spinner\n  .loader--spinner& {\n    margin-top: -30px;\n    margin-left: 5px;\n  }\n  // x\n  .loader--x& {\n    margin-top: 0px;\n    margin-left: 0px;\n    .filler {\n      width: 60px;\n    }\n  }\n  // animations\n  .loader.out();\n}\n\n// js\n\n.loader--js {\n  // disable animations\n  .spinner {\n    circle {\n      opacity: 1 !important;\n      .trans-anim-none() !important;\n    }\n  }\n  .filler {\n    span {\n      opacity: 1 !important;\n      .animation-none() !important;\n    }\n  }\n}\n\n// .media-loaded\n\n.media-loaded {\n  .loader {\n    .loader.out();\n  }\n}\n"