export default "/**\n * xtend extension: separator\n */\n\n.separator {\n  display: inline-block;\n  position: relative;\n  .margin(x, tiny);\n  font-size: .8em;\n  vertical-align: middle;\n}\n\n.separator--dash {\n  &:after {\n    content: '';\n  }\n}\n\n.separator--slash {\n  &:after {\n    content: '/';\n  }\n}\n\n.separator--bar {\n  &:after {\n    content: '|';\n  }\n}\n\n.separator--point {\n  &:after {\n    content: '';\n  }\n}\n\n.separator--arrow {\n  &:after {\n    content: '>';\n  }\n}\n"