export default "/**\n * btn\n */\n\n.btn {\n  .user-select--none();\n  cursor: pointer;\n  display: inline-flex;\n  flex-wrap: wrap;\n  align-items: center;\n  justify-content: space-between;\n  position: relative;\n  padding: 0;\n  z-index: @index--base; // fix :before negative z-index\n  line-height: 0;\n  &:hover, &.hover, &:active, &.active {\n    z-index: @index--active;\n  }\n  &.out {\n    z-index: @index--active - 1;\n  }\n}\n\n// custom\n\ndiv.btn {\n  // non interactive when div\n  cursor: default;\n  pointer-events: none;\n}\n\n.btn--close {\n  z-index: @index--btn-close;\n}\n"