export default ".demo--multilevel--default {\n\n  // content\n\n  .multilevel-title-wrapper {\n    .block();\n    .margin(y, layout) !important;\n    .margin(top, medium);\n    .margin(bottom, tiny);\n    position: relative;\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n  }\n\n  .multilevel-reset {\n    .caret.caret--horizontal.caret--inverse();\n  }\n\n  .multilevel-list {\n    // btn\n    .list-btn({\n      .btn--text();\n      .btn--tall();\n      .btn--none-x();\n      margin-bottom: -1px !important;\n      border-top-color: @gray--200 !important;\n      border-bottom-color: @gray--200 !important;\n    });\n    // caret\n    > li {\n      > button {\n        .caret();\n        .caret.caret--horizontal();\n      }\n    }\n  }\n\n  // toggle\n\n  .multilevel-body {\n    .block();\n    .toggle--block();\n    // animation\n    .anim-x();\n    animation-duration: @time--small;\n    &.inverse {\n      .anim-x-inverse();\n      animation-duration: @time--small;\n    }\n  }\n\n  .multilevel-tab {\n    .block();\n    .toggle--block();\n    // animation\n    .anim-x();\n    animation-duration: @time--small;\n    &.inverse {\n      .anim-x-inverse();\n      animation-duration: @time--small;\n    }\n  }\n\n}\n"