export default "/**\n * overlay\n */\n\n.overlay_outer {\n  .toggle--flex();\n  position: fixed;\n  z-index: @index--overlay;\n  top: 0;\n  left: 0;\n  bottom: 0; // @FIX no height or it bugs collapse animation\n  right: 0; // @FIX no width or it bugs collapse animation\n  // scroll\n  overflow: hidden;\n  .overflow-style();\n}\n\n.overlay {\n  display: flex;\n  flex-wrap: nowrap;\n  flex-direction: column;\n  justify-content: center;\n  margin: auto; // @FIX http://stackoverflow.com/questions/33454533/cant-scroll-to-top-of-flex-item-that-is-overflowing-container\n  width: 100%;\n  min-height: 100vh;\n  // space override @container--space\n  @media @max-sm {\n    padding: 0 !important;\n  }\n  // styles\n  .overlay_design {\n    .design-setup();\n  }\n}\n\n.overlay_inner {\n  .align-self(center);\n  position: relative;\n  z-index: @index--overlay;\n  width: 100%;\n}\n\n.overlay_content {\n  margin-top: auto;\n  margin-bottom: auto;\n}\n\n// addons\n\n.overlay--disable {\n  .xt-disable();\n  display: flex !important;\n  overflow: initial !important;\n  html.xt-scrollbar & {\n    overflow: initial !important;\n  }\n  position: static;\n  z-index: initial;\n  top: auto;\n  left: auto;\n  bottom: auto;\n  right: auto;\n  width: auto;\n  height: auto;\n  .overlay {\n    padding: 0;\n    width: auto;\n    min-height: auto;\n    .overlay_inner {\n      z-index: initial;\n      .overlay_design {\n        display: none;\n      }\n      .overlay_content {\n        padding: 0 !important;\n      }\n      .btn--close {\n        display: none !important;\n      }\n    }\n  }\n  > .backdrop {\n    display: none !important;\n  }\n}\n"