export default "@import '~xtend-library/src/core/overlay/overlay-setup.less';\n\n/**\n * vars\n */\n\n@overlays: tiny, small, medium, big, giant, full; // if you change this change also variables names below\n@overlay--tiny: @breakpoint--xs - 60;\n@overlay--small: @breakpoint--sm - 60;\n@overlay--medium: @breakpoint--md - 60;\n@overlay--big: @breakpoint--lg - 60;\n@overlay--giant: @breakpoint--xl - 60;\n@overlay--full: none;\n\n/**\n * overlay\n */\n\n.overlay {\n  // styles\n  .overlay_content {\n    padding: 3rem;\n    @media @min-sm {\n      padding: 4rem;\n    }\n    & + .overlay_content {\n      padding-top: 0 !important;\n    }\n  }\n  .overlay_design, .overlay_content {\n    border-radius: 4px;\n  }\n  .overlay_design {\n    border-width: 1px;\n    border-style: solid;\n    border-color: transparent;\n  }\n  .btn--close {\n    position: absolute;\n    top: 0;\n    right: 0;\n    .btn--giant();\n  }\n  // option\n  .overlay--light();\n  .overlay--size(small);\n}\n\n// addons\n\n.overlay--disable {\n  // disable custom animation\n  .overlay {\n    .overlay_inner, .overlay_content {\n      opacity: 1 !important;\n      .trans-anim-none() !important;\n    }\n  }\n}\n\n// variant\n\n.overlay--light {\n  .overlay_design {\n    border-color: @gray--300;\n    background: @white;\n    box-shadow: @shadow--light;\n  }\n  .overlay_inner {\n    color: @text;\n  }\n  .btn--close {\n    .btn--text();\n  }\n}\n\n.overlay--dark {\n  .overlay_design {\n    border-color: @gray--900;\n    background: @gray--900;\n    box-shadow: @shadow--dark;\n  }\n  .overlay_inner {\n    .text--inverse();\n  }\n  .btn--close {\n    .btn--text();\n  }\n}\n\n// special\n\n.overlay--squared {\n  .overlay_design, .overlay_content {\n    border-radius: 0;\n  }\n}\n\n.overlay--noborder {\n  .overlay_design {\n    border: 0;\n  }\n}\n\n.overlay--none {\n  .overlay_content {\n    padding: 0 !important;\n  }\n}\n\n// animation\n\n.overlay_outer {\n  animation-duration: @time--medium + @time--small; // needed for .out animation timing, put max animation time\n  &.active {\n    .overlay_design {\n      .anim-zoom.active();\n    }\n    .overlay_content, .btn--close {\n      .anim-y.active();\n      animation-delay: @time--small;\n    }\n    > .backdrop {\n      .backdrop.active();\n    }\n  }\n  &.out {\n    .overlay_design {\n      .anim-zoom.out();\n      animation-delay: @time--small;\n    }\n    .overlay_content, .btn--close {\n      .anim-y.out();\n    }\n    > .backdrop {\n      .backdrop.out();\n    }\n  }\n}\n"