export default "/**\n * group\n */\n\n.form-group {\n  position: relative;\n}\n\n// selectors\n\n.form-groups(@rules) {\n  .form-group {\n    @rules();\n  }\n}\n\n// addons\n\n.form-group--row {\n  display: flex;\n}\n\n.form-group--horizontal {\n  display: flex;\n  .form-label {\n    .margin(all, none);\n  }\n}\n\n.form-group--inline {\n  display: flex;\n  flex-wrap: nowrap;\n  .form-item {\n    width: auto;\n  }\n}\n\n/**\n * label\n */\n\n.form-label {\n  display: block;\n  position: relative;\n  cursor: pointer;\n  .user-select--none();\n}\n\n/**\n * item\n */\n\n.form-item {\n  .block();\n  // select arrow\n  select:not([multiple])& {\n    .padding(right, @select-space--size) !important;\n    white-space: normal;\n    background-position: calc(~'100% - '@select-space--size / 2) 50%, calc(~'100% - ('@select-space--size / 2~') + '@select-arrow--size) 50%, 100% 0;\n    background-size: @select-arrow--size @select-arrow--size, @select-arrow--size @select-arrow--size, @select-space--size 100%;\n    background-repeat: no-repeat;\n  }\n}\n\n// others\n\ntextarea {\n  overflow: hidden;\n  resize: none;\n}\n\n/**\n * check and radio\n */\n\n.checkbox, .radio {\n  position: relative !important;\n  &:not(.disabled) {\n    cursor: pointer;\n  }\n  // label\n  .form-label();\n  .checkradio-label();\n  // check and radio styled design\n  &:before, &:after {\n    content: '';\n    position: absolute !important;\n    top: 0;\n    left: 0;\n  }\n  &:before {\n    pointer-events: none;\n  }\n  &:after {\n    display: flex !important;\n    align-items: center;\n    justify-content: center;\n  }\n}\n\n// styled\n\ninput[type=\"checkbox\"]:not(.unstyled), input[type=\"radio\"]:not(.unstyled) {\n  position: absolute !important;\n  z-index: -1 !important;\n  opacity: 0 !important;\n}\n\ninput[type=\"checkbox\"]:not(.unstyled) {\n  & ~ .form-label {\n    .checkbox();\n  }\n}\n\ninput[type=\"radio\"]:not(.unstyled) {\n  & ~ .form-label {\n    .radio();\n  }\n}\n\n// unstyled\n\ninput[type=\"checkbox\"].unstyled, input[type=\"radio\"].unstyled {\n  display: block;\n  float: left;\n  & ~ .form-label {\n    display: block;\n    overflow: hidden;\n  }\n  & ~ .form-label {\n    .checkradio-label();\n  }\n}\n"