export default "/**\n * slider\n */\n\n// slider\n\n.slider {\n  position: relative;\n}\n\n.slides {\n  .user-select--none();\n  overflow: hidden;\n}\n\n// slides\n\n.slide {\n  position: relative;\n  > * {\n    display: block;\n    position: relative;\n  }\n}\n\n// .slider--nooverflow\n\n.slider--nooverflow {\n  .xt-disable();\n  .slides_inner {\n    // @FIX disable slider if not overflowing\n    .transform-none() !important;\n    .transition-none() !important;\n    justify-content: center !important;\n  }\n  .slider_pagination, [data-xt-nav] {\n    display: none !important;\n  }\n}\n\n// .slider--disable\n\n.slider--disable {\n  .xt-disable();\n  .slide:not(.active) {\n    .trans-anim-none() !important;\n    opacity: 0 !important;\n  }\n  .slider_pagination, [data-xt-nav], .loader, .filler, .spinner {\n    display: none !important;\n  }\n}\n\n// .slider--expand\n\n.slider--expand {\n  .xt-disable();\n  .slides {\n    overflow: visible;\n    height: auto !important;\n  }\n  .slides_inner {\n    .trans-anim-none() !important;\n    .transform-none() !important;\n  }\n  .slides_inner {\n    flex-wrap: wrap !important;\n  }\n  .slide:not(.active) {\n    opacity: 1 !important;\n  }\n  .xt-wrap, .slider_pagination, [data-xt-nav], .loader, .filler, .spinner {\n    display: none !important;\n  }\n}\n"