export default "// xtend extension: demo--overlay--content-multiple\n\n.demo--overlay--content-multiple {\n  .btn--close {\n    .btn--primary();\n  }\n  .overlay_menu {\n    .list();\n    .list-space(none);\n    height: 63px;\n    background: @primary;\n    > .btn {\n      display: flex;\n      align-items: center;\n    }\n  }\n  // animation\n  &.active {\n    .overlay_menu {\n      .anim-y.active();\n      animation-delay: @time--small;\n    }\n  }\n  &.out {\n    .overlay_menu {\n      .anim-y-inverse.out();\n    }\n  }\n}\n"