export default "@import '~xtend-library/src/extension/slider/slider-setup.less';\n\n/**\n * slider\n */\n\n// slides\n\n.slides {\n  .block();\n  &.xt-autoHeight {\n    // autoHeight animations\n    transition-property: height;\n    transition-duration: 750ms;\n    transition-timing-function: @bez--out;\n  }\n}\n\n.slides_inner {\n  .row();\n  .row-space(medium);\n  flex-wrap: nowrap;\n  align-items: flex-start;\n  // dragger animations\n  transition-property: transform;\n  transition-duration: 750ms;\n  transition-timing-function: @bez--out;\n}\n\n// pagination\n\n.slider_pagination {\n  .list();\n  .list-space(small);\n  align-items: center;\n  justify-content: center;\n  .padding(top, medium);\n}\n\n[data-xt-pag].btn {\n  .btn--tiny();\n  border-radius: 2rem;\n  padding: .5em 1.15em;\n  min-height: 2.2rem;\n  &.active {\n    padding: .5em 2em;\n  }\n}\n\n// navigation\n\n[data-xt-nav].btn {\n  .btn--tiny();\n  padding: .5em 1.15em;\n  min-height: 2.2rem;\n  border-radius: 2rem;\n  [class^='icon-'], [class*=' icon-'] {\n    .icon--small();\n  }\n}\n"