export default "/**\n * logo-icon\n */\n\n.logo-icon {\n  .link--none();\n  display: inline-flex;\n  .align-items(center);\n  height: 100%;\n  .site_header & {\n    .padding(y, 1.5rem);\n  }\n  img {\n    width: 2rem;\n    height: 1.4rem;\n    .site_header & {\n      width: 3.4rem;\n      height: 2.4rem;\n    }\n  }\n  // animation\n  img {\n    transition-property: opacity;\n    transition-duration: @time--small;\n    transition-timing-function: @bez--out;\n  }\n  &:hover, &.hover, &:active {\n    img {\n      opacity: 0.9;\n    }\n  }\n}\n\n/**\n * .btn--site_multiline\n */\n\n.btn--site_multiline {\n  .link--none();\n  .btn--squared();\n  .btn--noborder();\n  .btn--nodesign();\n  .btn--noeffect();\n  padding: 0;\n  .font-sans--regular();\n  font-size: 1.4rem;\n  color: @text;\n  text-transform: uppercase;\n  .btn--site_multiline_line {\n    display: inline-block;\n    .margin(y, 1px);\n    padding: .25em .5em;\n    [class^='icon-'], [class*=' icon-'] {\n      position: relative;\n      font-size: .75em;\n      .btn--left& {\n        top: -1px;\n        left: -1px;\n      }\n      .btn--right& {\n        top: -1px;\n        left: 1px;\n      }\n    }\n  }\n  .btn--site_multiline_special {\n    background: linear-gradient(135deg, @grad--light);\n    .btn--left& {\n      .margin(left, .5em);\n    }\n    .btn--right& {\n      .margin(right, .5em);\n    }\n  }\n  .btn--site_multiline_special_text {\n    display: none;\n  }\n  &:hover, &.hover, &:active, &.active {\n    color: @text;\n    .btn--site_multiline_line {\n      background: linear-gradient(135deg, @grad--light);\n    }\n    .btn--site_multiline_special {\n      .margin(left, 0);\n      .margin(right, 0);\n      background: linear-gradient(135deg, @grad--primary);\n      color: @white;\n    }\n    .btn--site_multiline_special_text {\n      display: inline-block;\n      .btn--left& {\n        .margin(left, .5em);\n      }\n      .btn--right& {\n        .margin(right, .5em);\n      }\n    }\n  }\n}\n\n/**\n * .make-line\n */\n\n.make-line {\n  position: relative;\n  z-index: 1;\n  .make-line_container {\n    display: block;\n    position: relative;\n    width: 100%;\n    .make-line_line {\n      .link--none();\n      display: inline-block;\n      padding-right: 15px;\n      background: @white;\n    }\n    .make-line_line:after {\n      content: '';\n      position: absolute;\n      z-index: -1;\n      bottom: .5em;\n      left: 0;\n      width: 100%;\n      height: 2px;\n      background: @gray--200;\n    }\n  }\n  // animation\n  .make-line_container {\n    .make-line_line:after {\n      left: 100%;\n      width: 0;\n      opacity: 0;\n      transition: all @time--medium @bez--out;\n    }\n  }\n  &:hover, &.hover, &:active, &.active {\n    .make-line_container {\n      .make-line_line:after {\n        left: 0;\n        width: 100%;\n        opacity: 1;\n        animation: anim-opacity--in @time--medium @bez--in, line--in @time--medium @bez--in;\n      }\n    }\n  }\n}\n\n@keyframes line--in {\n  0% {\n    left: 0;\n    width: 0;\n  }\n  100% {\n    left: 0;\n    width: 100%;\n  }\n}\n\n/**\n * .make-anchor\n */\n\n.make-anchor {\n  position: relative;\n  width: 100%;\n  a {\n    display: block;\n    .link--none();\n    color: @text !important;\n  }\n  .site_article_anchor {\n    pointer-events: none;\n  }\n  // animation\n  .site_article_anchor {\n    opacity: 0;\n    transition: all @time--medium @bez--out;\n  }\n  &:hover, &.hover, &:active, &.active {\n    a {\n      color: @primary !important;\n    }\n    .site_article_anchor {\n      opacity: 1;\n      animation: anim-opacity--in @time--medium @bez--in;\n    }\n  }\n}\n\n.site_article_anchor {\n  position: absolute;\n  z-index: 2;\n  bottom: 0;\n  right: 12px;\n  h2 & {\n    bottom: 3px;\n  }\n  .btn {\n    .link--none();\n    .btn--squared();\n    .btn--noborder();\n    .btn--noeffect();\n    padding: 7px;\n    width: 30px;\n    height: 30px;\n    border-radius: 50%;\n    border-color: @white !important;\n    background: @white !important;\n    font-size: 17px;\n    color: @primary;\n    line-height: 1;\n  }\n}\n"