export default "/**\n * xtend extension: overlay-class-html\n */\n\n.overlay-class-html { // @NOTICE DO NOT APPLY to selector with descendants with position: fixed;\n  transition-property: transform;\n  transition-duration: @time--medium;\n  transition-timing-function: @bez--out;\n  html.overlay-class-html--top & {\n    transform: translateY(3rem);\n    transition-timing-function: @bez--in;\n  }\n  html.overlay-class-html--left & {\n    transform: translateX(3rem);\n    transition-timing-function: @bez--in;\n  }\n  html.overlay-class-html--right & {\n    transform: translateX(-3rem);\n    transition-timing-function: @bez--in;\n  }\n}\n"